p{
  font-size: 17px;
  color: var(--colorJ);
}

li {
  font-size: 22px;
  line-height: 1.5;
}

button{
  background-color: var(--colorJ);
  color:var(--colorA);
  font-weight: bolder;
  width: 80px;
  height: 40px;
  border-radius: 5%;
}

/* Define las variables para los colores */
:root{
    --colorA: #ffffff;
    --colorB: #979797;
    --colorC: #000000;
    --colorD: #7ad7f3;
    --colorE: #0c09cc;
    --colorF: #09bd1b;
    --colorG: #e43333;
    --colorH: #ECF4D6;
    --colorI: #9AD0C2;
    --colorJ: #2D9596;
    --colorK: #265073;
}

body{
     /* Definimos el contenedor principal como un grid */ 
     display: grid;
     /* Definimos la separación entre cada una de las zonas */
     grid-gap: 0px;
     /* Definimos el número de filas y columnas que conforman nuestra maqueta*/
     grid-template-columns: 100%;
     grid-template-rows: 130px 800px 110px;
     /* Definimos mediante areas nuestra maqueta*/
     grid-template-areas:
        "header"
        "zona_1"
        "footer"; 
      background: url('../src/imagenes/cuaderno.png') top center/60% 100% no-repeat ;   
      background-color: white;
      margin-top: 5px;
    }

/* Área de Cabecera */   
header{
    grid-area:header;
    display: flex;
    flex-direction: row;
    flex-wrap:nowrap;
    align-items: center;
    justify-content: space-evenly;
}

#header_izq {
  width: 32%;
  text-align: right;
}

#header_der {
  width: 25%;
}

#entrada {
  display: flex;
  flex-direction: row;
  flex-wrap:nowrap;
  align-items: center;
  justify-content: space-evenly;
  width: 43%;
}

input {
  width: 30vw;
  height: 3vh;
  font-size: 25.05px;
}

/* Subzona de contenido */
#contenedor_zona_1{
    grid-area:zona_1;
    display: flex;
    flex-direction: row;
    flex-wrap:nowrap;
    align-items: center;
    justify-content: space-evenly;
}

#izq {
  width: 32%;
}

#salida {
  display: flex;
  flex-direction: row;
  flex-wrap:nowrap;
  align-items: top;
  justify-content: left;
  width: 46%;
  height: 100%;
}

#der {
  width: 22%;
}

/* Pie de página */
footer{
    grid-area:footer;
    display: flex;
    flex-direction: row;
    flex-wrap:nowrap;
    align-items: center;
    justify-content: space-evenly;
}

/* Adaptación a la resolución de la pantalla  */ 
@media screen and (max-width:1200px) {
   
    p{
      font-size: 15px;
    }
    li {
      font-size: 19px;
      line-height: 1.75;
    }
    button{
      font-size: 12px;;
      width: 60px;
      height: 30px;
    }   
    h2{
      font-size: 17px;
    } 
    input {
      width: 22vw;
      height: 2.5vh;
      font-size: 19px;
    }
}

@media screen and (max-width:992px) {
  
  body{
    background: url('../src/imagenes/cuaderno.png') top center/90% 100% no-repeat; 
    background-color: white;
  }

  #header_izq {
    width: 23%;
  }
  
  #header_der {
    width: 10%;
  }
  
  #entrada {
    width: 67%;
  }
  #izq {
    width: 22%;
  }
  
  #salida {
    width: 68%;
  }
  
  #der {
    width: 10%;
  }
  input {
    width: 40vw;

  }

}
